.agog-tooltip {
    position: relative;
}
.agog-tooltip-root {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    z-index: 100;
    background-color: rgba(51, 51, 51, .8);
    color: #fff;
    white-space: nowrap;
    padding: 3px 15px 6px;
    border-radius: 5px;
    transition: opacity 200ms cubic-bezier(0, 0, 0.2, 1) 0ms;
}
.agog-tooltip:hover .agog-tooltip-root {
    opacity: 1;
    visibility: visible;
    transition: opacity 200ms cubic-bezier(0, 0, 0.2, 1) 0ms;
}
.agog-tooltip-root:after, .agog-tooltip-root:before {
    border: solid transparent;
    content: "";
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
}

.agog-tooltip-root:after {
    border-color: rgba(51, 51, 51, 0);
    border-right-color:  rgba(51, 51, 51, .8);
    border-width: 8px;
    margin-top: -8px;
}

.agog-tooltip-root:before {
    border-color: rgba(204, 204, 204, 0);
    border-width: 9px;
    margin-top: -9px;
}

.agog-tooltip-root.topLeft:after, .agog-tooltip-root.topLeft:before {
    left: 15px;
    top: 100%;
    margin-top: 0px;
    transform: rotate(-90deg);
}

.agog-tooltip-root.topCenter:after, .agog-tooltip-root.topCenter:before {
    right: calc(50% - 5px);
    top: 100%;
    margin-top: 0px;
    transform: rotate(-90deg);
}

.agog-tooltip-root.topRight:after, .agog-tooltip-root.topRight:before {
    right: 15px;
    top: 100%;
    margin-top: 0px;
    transform: rotate(-90deg);
}

.agog-tooltip-root.rightTop:after, .agog-tooltip-root.rightTop:before {
    right: 100%;
    top: 16px;
}

.agog-tooltip-root.rightMiddle:after, .agog-tooltip-root.rightMiddle:before {
    right: 100%;
    top: 50%;
}

.agog-tooltip-root.rightBottom:after, .agog-tooltip-root.rightBottom:before {
    right: 100%;
    bottom: 10px;
}

.agog-tooltip-root.leftTop:after, .agog-tooltip-root.leftTop:before {
    left: 100%;
    top: 16px;
    transform: rotate(-180deg);
}

.agog-tooltip-root.leftMiddle:after, .agog-tooltip-root.leftMiddle:before {
    left: 100%;
    top: 50%;
    transform: rotate(-180deg);
}

.agog-tooltip-root.leftBottom:after, .agog-tooltip-root.leftBottom:before {
    left: 100%;
    bottom: 10px;
    transform: rotate(-180deg);
}

.agog-tooltip-root.bottomLeft:after, .agog-tooltip-root.bottomLeft:before {
    left: 15px;
    bottom: 100%;
    margin-top: 0px;
    transform: rotate(90deg);
}

.agog-tooltip-root.bottomCenter:after, .agog-tooltip-root.bottomCenter:before {
    right: calc(50% - 5px);
    bottom: 100%;
    margin-top: 0px;
    transform: rotate(90deg);
}

.agog-tooltip-root.bottomRight:after, .agog-tooltip-root.bottomRight:before {
    right: 15px;
    bottom: 100%;
    margin-top: 0px;
    transform: rotate(90deg);
}