.loading-container {
  height: 30px;
  width: 30px;
}
.loading-container svg {
  height: 100%;
  width: 100%;
}
#loading {
    animation: change-color 3s infinite;;
  }
  
  @keyframes change-color {
    0% {
      fill: rgba(255, 255, 255, 1);
    }
    50% {
      fill: rgba(255, 255, 255, .5);
    }
    100% {
      fill: rgba(255, 255, 255, 1);
    }
  }