.agogButton {
  overflow: hidden;
  display: inline-flex;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: center;
  justify-content: center;
  position: relative;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
  outline: 0px;
  border: 1px solid transparent;
  margin: 0px;
  cursor: pointer;
  user-select: none;
  vertical-align: middle;
  appearance: none;
  text-decoration: none;
  font-family: Roboto, Helvetica, Arial, sans-serif;
  font-weight: 500;
  font-size: 0.875rem;
  line-height: 1.75;
  letter-spacing: 0.02857em;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, border-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  /* box-shadow: rgb(0 0 0 / 20%) 0px 3px 1px -2px, rgb(0 0 0 / 14%) 0px 2px 2px 0px, rgb(0 0 0 / 12%) 0px 1px 5px 0px; */
}


.rippleCont {
  position: relative;
  overflow: hidden;
  display: inline-block;
}
.rippleCont span.ripple {
  position: absolute;
  border-radius: 50%;
  transform: scale(0);
  opacity: .4;
  animation: ripple 500ms linear;
  background-color: rgb(255, 255, 255);
}

@keyframes ripple {
  to {
    transform: scale(3);
    opacity: 0;
  }
}

.agog-button-primary {
  color: rgb(255, 255, 255);
  background-color: rgb(25, 118, 210);
}
.agog-button-primary:hover {
  background-color: rgba(25, 118, 210, .7);
}
.agog-button-transparent {
  background-color: transparent;
}
.agog-button-default {
  border: 1px solid #d9d9d9;
  background-color: #fff;
}
.agog-button-default:hover {
  border-color: rgb(25, 118, 210);
  color: rgb(25, 118, 210);
}
.agog-button-dashed {
  border: 1px dashed #d9d9d9;
  background-color: #fff;
}
.agog-button-dashed:hover {
  border-color: rgb(25, 118, 210);
  color: rgb(25, 118, 210);
}
.agog-button-link {
  color: rgba(25, 118, 210, 1);
  background-color: transparent;
}
.agog-button-link:hover {
  color: rgba(25, 118, 210, .5);
}
.agog-button-text {
  background-color: transparent;
}
.agog-button-text:hover {
  background-color: rgba(0,0,0,.05);
}
.agog-button-size-small {
  padding: 3px 8px;
}
.agog-button-size-middle {
  padding: 6px 16px;
}
.agog-button-size-large {
  padding: 9px 24px;
}
.agog-button-shape-round {
  border-radius: 24px;
}
.agog-button-shape-default {
  border-radius: 4px;
}
.agog-button-shape-circle {
  border-radius: 50%;
  padding-inline-start: 14px;
  padding-inline-end: 14px;
}
button[disabled], button[disabled]:hover{
  background-color: #eaeaea;
  border-color: #e0e0e0;
  color: #c0c0c0;
  cursor: not-allowed;
}